import Vue from 'vue';
import Router from 'vue-router';
import Hello from '@/components/Hello';
import hoode from '@/components/hoode/hoode';
import sey from '@/components/sey/sey';
import show from '@/components/show';

Vue.use(Router)


export default new Router({
  routes: [{
      path: '/',
      name: 'hoode',
      component: hoode
    },{
      path: '/sey',
      name: 'sey',
      component: sey
    },{
      path: '/show',
      name: 'show',
      component: show
    }
  ]
})
//redirect   默认值



