<template>
	<div class="sey">
		<input type="text" v-model='newData.name' />
			<input type="button" value="添加" @click="addBtn(newData)"/>
			<span>({{total}})</span>
		<ul>
			<li v-for="(item,index) in todos" :class="{avtive:item.completed}">
				{{item.name}}
				<button @click='removeBtn(index)'>删除</button>
				<button @click='success(index)'>{{item.completed?'已完成':'完成'}}</button>
			</li>
		</ul>
	</div>
</template>

<script>
	export default {
		name: 'sey',
		data() {
			return {
				el: '.sey',
				newData:{
						id:null,
						name:'',
						completed:false
				},
				todos: [{
						'id': 1,
						'name': 'hello world',
						'completed': false
					},
					{
						'id': 2,
						'name': 'hello world',
						'completed': false
					},
					{
						'id': 3,
						'name': 'hello world',
						'completed': false
					},
					{
						'id': 4,
						'name': 'hello world',
						'completed': false
					},
					{
						'id': 5,
						'name': 'hello world',
						'completed': false
					}
				],
			}
		},
		methods:{
					addBtn(data){
						this.newData={id:null,name:''};
							this.todos.push(data);
					},
				removeBtn(index){
						this.todos.splice(index,1)
				},
				success(index){
						this.todos[index].completed = !this.todos[index].completed
				}
  },
  computed:{
					total(){
						return this.todos.length;
					}
				}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
	* {
		margin: 0;
		padding: 0;
		list-style: none;
	}

	ul li {
		width: 500px;
		height: 50px;
		padding-left: 50px;
		line-height: 50px;
		border: 1px solid brown;
		margin: 10px;
	}

	ul li button {
		width: 100px;
		float: right;
		line-height: 50px;
		border: 0;
		cursor: pointer;
	}

	ul li button:nth-of-type(1) {
		background: black;
		color: white;
	}

	ul li button:nth-of-type(2) {
		background: green;
	}

	.avtive {
		text-decoration: line-through;
	}
</style>



