<template>
  <div class="hoode">
    <!--<p>{{message}}---{{info}}</p>-->
			<p>
				<input type="type" name="" id="" value="" v-model="motext"/><br />
				{{motext}}
			</p>
  </div>
</template>

<script>
export default {
  name: 'hoode',
  	data() {
			return {
				el:'.hodde',
				motext:''
			}
		}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
	p{
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
	}
</style>



