<template>
  <div id="app">
  	<v-hello></v-hello>
     	 <div class='tab'>
          <div class='tab-item'><router-link to="/">双向绑定</router-link></div>
          <div class='tab-item'><router-link to="/sey">dom操作</router-link></div>
          <div class='tab-item'><router-link to="/show">显示隐藏</router-link></div>
          <!--<div class='tab-item'><router-link to="/seller">商家</router-link></div>-->
      </div>
    <br/>
    <router-view>
    	  <hoode></hoode>
    	  <sey></sey>
    </router-view>
  </div>
</template>

<script>
//import hello from './components/hello';
	export default {
		components: {
//			"v-hello": hello
		},
		data() {
			return {
				seller: {}
			}
		},
		created() {
			this.$http.get("/api/hotelData").then(function(res) {
				res = res.hotelData;
				this.seller = res.data;
			})
		}
	}
</script>

<style>
  .tab{
    width:100%;
    background: #000000;
  }
  .tab a{
    float:left;
    text-decoration: none;
    padding-left: 20px;
  }
</style>



