<template>
	<div class="show">
		<input type="checkbox" v-model="isHide"/>
		<div class="isShow" v-show="isHide">
			
		</div>
	</div>
</template>

<script>
	export default {
		name: 'sey',
		data() {
			return {
				el:'.show',
				isHide:false
			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
	.isShow{
		width: 200px;
		height: 200px;
		background: #ddd;
	}
</style>


